    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.3 Tcnicas de seleccin y separacin de animales

    p.mb-4 Son procedimientos obligatorios de manejo que se aplican en la etapa de precargue y durante el transporte, respectivamente. Ambos buscan garantizar el bienestar animal y prevenir el sufrimiento, las lesiones y el conflicto social entre los animales transportados.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tcnicas de seleccin (exclusin sanitaria y de bienestar)

    .BGM01.px-md-5.px-4.mb-4
      p.mb-4 La #[b seleccin] es la tcnica que se aplica en el #[b precargue] para determinar la aptitud del animal para viajar, siendo una #[b obligacin del propietario/tenedor.] Dentro de estas tcnicas se encuentran las siguientes:

      .row.justify-content-center.mb-0
        .col-lg-10
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 8.
            i Tcnicas de seleccin
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de observacin
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Criterio de exclusin (animal no apto)
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Observacin de salud y lesiones]
                  td(style= "background-color: #FFFF") Se excluyen animales #[b enfermos, lesionados] o con #[b prolapsos] (rectal, vaginal o uterino), ya que su condicin compromete el bienestar.
                  td(style= "background-color: #FFFF") #[b "Animales enfermos, lesionados o con prolapsos] (rectal, vaginal o uterino)". Manual de procedimiento (Nm. 4.a.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Observacin de movilidad]
                  td(style= "background-color: #FFFF") Se excluyen animales que #[b no puedan permanecer en la posicin de estacin de equilibrio esttico animal sin ayuda] (incapacidad de mantenerse de pie), pues sern vulnerables en ruta.
                  td(style= "background-color: #FFFF") #[b "Animales que no puedan permanecer en la posicin de estacin de equilibrio esttico animal sin ayuda".] Manual de procedimiento (Nm. 4.a.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Observacin fisiolgica]
                  td(style= "background-color: #FFFF") Se excluyen #[b hembras prximas al parto (ltimo tercio de gestacin)] o aquellas que hayan parido #[b siete (7) das antes] del embarque, por el riesgo de sufrimiento en esta etapa crtica.
                  td(style= "background-color: #FFFF") #[b "Hembras prximas al parto (ltimo tercio de gestacin)] o que hayan parido #[b siete (7) das antes] de la fecha del embarque". Manual de procedimiento (Nm. 4.a.i).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tcnicas de separacin (organizacin de la carga)

    p.mb-4 La #[b separacin] es la tcnica que se aplica al #[b momento del cargue] y durante el #[b trayecto] para mitigar la hostilidad y las lesiones fsicas entre los grupos que s son aptos para el transporte. Esta tcnica requiere el uso de implementos estructurales y se divide en:

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 A. Requisito estructural (implemento)

    p La tcnica de separacin exige que el vehculo cuente con las herramientas fsicas necesarias.
    p.mb-4 #[b Implemento:] divisiones o barreras fuertes.

    .row.justify-content-center.align-items-center.mb-4
