            .text-white.p-4
              p.text-center.mb-0(style="margin-top: 42px;") #[b Soporte documental:] "Contar con #[b divisiones o barreras] fuertes que permitan la #[b separacin de los animales"]. Manual de procedimiento (Nm. 2.2.c.iii).

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 B. Criterios para la ejecucin de la separacin

    p.mb-4 La tcnica se aplica segregando a los animales segn sus caractersticas fsicas y comportamentales, aplicando los siguientes detalles por criterio:

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 9.
          i Criterios de separacin
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Criterio de separacin
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle de la tcnica de agrupacin
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Por especie]
                td(style= "background-color: #FFFF") Separar los animales de diferentes especies para evitar el miedo e interacciones agresivas (ej. bovinos de porcinos).
                td(style= "background-color: #FFFF") #[b "Separar por especie..."]. Manual de procedimiento (Nm. 4.c.iii).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Por tamao y etreos]
                td(style= "background-color: #FFFF") Separar por #[b edad y tamao] para proteger a los animales ms pequeos o jvenes de ser aplastados o golpeados por los ms grandes.
                td(style= "background-color: #FFFF") #[b "Separar por... edad, tamao..."]. Manual de procedimiento (Nm. 4.c.iii).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Por sexo y hostilidad]
                td(style= "background-color: #FFFF") Separar a #[b machos sexualmente maduros de hembras] para evitar el acoso y separar a los #[b animales con y sin cuernos] para prevenir heridas punzantes.
                td(style= "background-color: #FFFF") #[b "Separar por... machos sexualmente maduros de hembras, animales con y sin cuernos], animales que puedan ser hostiles con otros...". Manual de procedimiento (Nm. 4.c.iii).

