      .col-lg-9.order-lg-1.order-2.mb-lg-0
        .row
          p Estos mtodos dentro de la unidad de transporte son el conjunto de tcnicas operacionales y estructurales que buscan organizar la carga viva para garantizar la #[b estabilidad del vehculo] y el #[b bienestar animal], minimizando el conflicto social, las lesiones y el hacinamiento.
          p.mb-4 La distribucin efectiva se logra mediante dos tcnicas principales: 
          .col-lg-6.mb-4.mb-lg-0(data-aos="fade-right")
            .tarjeta-avatar-b.h-100
              .tarjeta-avatar-b__img2
