      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Mtodo de control de densidad (distribucin espacial)

    p.mb-4 Este mtodo asegura que cada animal tenga el #[b espacio fsico y funcional] suficiente para tolerar el trayecto. Se basa en el clculo de la densidad de carga y la disposicin del animal en el espacio, teniendo en cuenta:

