          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Aspecto del mtodo
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Requisito tcnico para la distribucin 
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Requisito funcional]
                td(style= "background-color: #FFFF") La distribucin debe procurar que se cuente con #[b espacio disponible] para que el animal pueda realizar los movimientos vitales.
                td(style= "background-color: #FFFF") "...procurar que se cuente con #[b espacio disponible] para que el animal pueda #[b levantarse, acostarse, dar vuelta..."]. Manual de procedimiento (Nm. 4.e).
              tr(data-aos="fade-right")
