                td(style= "background-color: #FFFF") La distribucin del peso de los animales debe ser #[b equilibrada] dentro del vehculo, distribuyendo la carga de manera uniforme para evitar desestabilizar la unidad durante la conduccin.
                td(style= "background-color: #FFFF") Implcito en el requerimiento de densidad de carga para mantener la posicin de equilibrio esttico animal.
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Distribucin de especies menores]
                td(style= "background-color: #FFFF") Para especies transportadas en unidades (ej. pollitos), la distribucin es que la caja acte como unidad de espacio y que esta, a su vez, est #[b fijada] correctamente dentro del vehculo.
                td(style= "background-color: #FFFF") "Estar #[b fijados o dispuestos de manera tal, que se evite su desplazamiento y/o volcamiento"]. Resolucin 20223040006915 (Nm. 2.2.c.v).

