                  td(style= "background-color: #FFFF") "Ser #[b seguros], tener un #[b adecuado estado] y elaborados de #[b materiales resistentes al impacto..."]. Resolucin 20223040006915 (Nm. 2.2.c.i).
                tr(data-aos="fade-right")
