                  td(style= "background-color: #FFFF") Las superficies internas deben ser #[b lisas y sin salientes ni bordes puntiagudos] que anulen la seguridad, ya que estos pueden causar lesiones graves.
                  td(style= "background-color: #FFFF") "Sern #[b lisas, seguras, sin salientes, ni bordes puntiagudos] que puedan causar lesiones...". Resolucin 20223040006915 (Nm. 2.2.d.i).
      
      .row.justify-content-center(titulo="Mtodo de aseguramiento de grupos (separacin interna)")
        .col-lg-12.order-lg-1.order-2
          p La tcnica de aseguramiento de grupos se logra mediante la instalacin de #[b barreras o divisiones] que restringen el movimiento excesivo, previenen el hacinamiento y anulan la hostilidad entre grupos de diferente riesgo. En dicho mtodo, se debe tener en cuenta:
        .col-lg-10.order-lg-1.order-2
