              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Uso de divisiones]
                  td(style= "background-color: #FFFF") La unidad de transporte debe contar con #[b divisiones o barreras fuertes] que permitan la separacin y el aseguramiento de los grupos homogneos.
                  td(style= "background-color: #FFFF") "Contar con #[b divisiones o barreras fuertes] que permitan la #[b separacin de los animales."]. Manual de procedimiento (Nm. 2.2.c.iii).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Fijacin de grupos hostiles]
                  td(style= "background-color: #FFFF") El aseguramiento debe ser tal que se separen animales con potencial de dao, tales como #[b animales con y sin cuernos y machos sexualmente maduros de hembras], evitando que se muevan libremente y se agredan.
                  td(style= "background-color: #FFFF") "Separar por... #[b machos sexualmente maduros de hembras, animales con y sin cuernos], animales que puedan ser hostiles con otros...". Manual de procedimiento (Nm. 4.c.iii).

      .row.justify-content-center(titulo="Mtodo de aseguramiento de unidades de contencin")
