              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Fijacin de cajas (aves)]
                  td(style= "background-color: #FFFF") Las cajas plsticas o de cartn deben estar #[b fijadas o dispuestas] dentro del vehculo de tal manera que se #[b evite su desplazamiento] o volcamiento durante la conduccin.
                  td(style= "background-color: #FFFF") "Estar #[b fijados o dispuestos de manera tal, que se evite su desplazamiento y/o volcamiento"]. Resolucin 20223040006915 (Nm. 2.2.c.v).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Aseguramiento contra la inercia]
                  td(style= "background-color: #FFFF") Este mtodo de aseguramiento previene que la #[b conduccin brusca] (aceleracin o frenado) desestabilice las pilas de contenedores, lo que podra causar aplastamiento o asfixia en la carga.
                  td(style= "background-color: #FFFF") Implcito en la necesidad de fijar y en el requisito de conduccin suave. Resolucin 20223040006915 (Nm. 4).

    p.mb-4 Asimismo, dentro de los mtodos de aseguramiento se incluye el #[b mtodo de aseguramiento del equilibrio], cuyo principio es garantizar que la carga en este caso, los animales pueda mantenerse en pie por s misma, condicin que define a un animal apto. Para ello, se consideran las siguientes acciones:

    .row.justify-content-center
      .col-lg-5.col-sm-6.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.color-primario.p-4
          .row.justify-content-center.mb-3
            .col-5
              figure.mb-4
                img(src='@/assets/curso/temas/tema1/img-36.svg', alt="Imagen decorativa")
          h4 Piso antideslizante
          p.mb-0 El implemento de seguridad (piso) debe ser antideslizante para que el animal mantenga el equilibrio esttico y no pierda la capacidad de aseguramiento individual.
      .col-lg-5.col-sm-6.mb-4.mb-lg-0(data-aos="fade-left")
        .tarjeta.color-primario.p-4
          .row.justify-content-center.mb-3
            .col-5
              figure.mb-4
                img(src='@/assets/curso/temas/tema1/img-37.svg', alt="Imagen decorativa")
          h4 Conduccin suave
          p.mb-0 La conduccin suave y progresiva es el mtodo de aseguramiento operacional que minimiza las fuerzas externas que podran desequilibrar a los animales.

    Separador

    #t_1_6.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.6 Tipos de precintos

    .row.justify-content-center.mb-4
      .col-lg-9.mb-4.mb-lg-0
        p Los #[b precintos] (o sellos de seguridad) son elementos fsicos de control que se utilizan en el transporte terrestre para garantizar la #[b integridad] de la unidad de carga o de los contenedores, asegurando que la carga viva no ha sido manipulada, alterada, ni ha escapado desde el momento de la inspeccin inicial.
        .bg1.tarjeta.p-4
          .row.justify-content-center.align-items-center
            .col-lg-1.p-0.d-none.d-lg-block
              figure
                img.img-fluid(src="@/assets/curso/temas/tema1/img-20.svg" alt="Icono 1")
            .col-lg-11
              p.mb-0 Si bien la #[b Resolucin 20223040006915] (Manual de procedimientos) no detalla una clasificacin exhaustiva de los tipos de precintos, su uso se fundamenta en la exigencia de control #[b oficial] por parte del #[b Instituto Colombiano Agropecuario (ICA)] y las #[b autoridades de trnsito.]
      .col-lg-3
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img-19.png" alt="Icono 1")

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Concepto y funcin principal del precinto

    .BGM01.px-md-5.px-4.mb-4
      p.mb-4 El precinto es un #[b dispositivo de un solo uso] que, al ser colocado, indica que la unidad ha sido verificada y no debe abrirse sin autorizacin. Por consiguiente, se incluyen las siguientes:

      .row.justify-content-center.mb-0
        .col-lg-10
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 15.
            i Funciones del precinto
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Aspecto
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle de la funcin
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte normativo (base del control)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Funcin sanitaria]
                  td(style= "background-color: #FFFF") Garantizar que, despus de la inspeccin de la #[b Gua Sanitaria de Movilizacin (GSM)], la carga permanezca inalterada, previniendo la posible introduccin de animales no aptos o la mezcla de lotes.
                  td(style= "background-color: #FFFF") La #[b obligacin de permitir la inspeccin, vigilancia y control] por parte del #[b ICA] implica el uso de precintos como medida de aseguramiento posterior. Resolucin 20223040006915 (Art. 4, Pargrafo).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Funcin de seguridad]
                  td(style= "background-color: #FFFF") Asegurar que las puertas, rampas o compuertas de la unidad de transporte permanezcan 3[b cerradas] durante el trayecto, evitando el riesgo de escape de animales o la alteracin de la carga.
                  td(style= "background-color: #FFFF") El vehculo debe ser #[b "seguro"] y estar diseado para la #[b contencin de animales vivos]. Resolucin 20223040006915 (Nm. 2.2.c.i).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Clasificacin de precintos segn su aplicacin en el transporte

    p.mb-4 Los precintos pueden clasificarse segn el punto donde se colocan para asegurar la carga o la unidad de la siguiente manera:

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 16.
          i Clasificacin de precintos
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tipo de precinto (clasificacin por aplicacin)
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Punto de colocacin / uso
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Propsito especfico
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Precinto de puertas y rampas]
                td(style= "background-color: #FFFF") Colocado en los #[b mecanismos de cierre] de las puertas de la carrocera o las rampas de acceso al compartimento de animales.
                td(style= "background-color: #FFFF") Asegurar la #[b contencin fsica] de la carga viva e impedir el acceso no autorizado a los animales en ruta.
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Precinto sanitario de contenedor]
                td(style= "background-color: #FFFF") Utilizado para sellar las 3[b cajas o guacales] de transporte, especialmente en especies pequeas (aves).
                td(style= "background-color: #FFFF") Asegurar la #[b integridad del lote] y la #[b cantidad de animales] certificada en el guacal, previniendo el intercambio de animales.
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Precinto oficial (general)]
                td(style= "background-color: #FFFF") Usado por el #[b ICA] en el punto de origen o en un puesto de control para indicar que el vehculo ha sido #[b inspeccionado] y cumple con las condiciones sanitarias.
                td(style= "background-color: #FFFF") Sello visible de la autoridad que certifica el inicio legal de la movilizacin.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tcnicas de manejo de precintos

    p.mb-4 El manejo adecuado de los precintos es una obligacin de procedimiento que recae sobre el transportador y posee las siguientes tcnicas:
    
    TabsC.color-acento-botones(data-aos="zoom-out")
      .bg-color-2.py-3.py-md-4(titulo="Tcnica de verificacin inicial")
        .row.justify-content-center.p-4
          .col-lg-10.col-12.order-lg-2.order-2
            p El conductor debe #[b verificar la correcta colocacin] del precinto antes de iniciar el viaje, asegurando que el nmero de precinto coincida con el registro documental (si aplica).  
            figure
              img(src='@/assets/curso/temas/tema1/img-21.png' alt="Imagen decorativa")            
      .bg-color-2.py-3.py-md-4(titulo="Tcnica de rotura controlada")
        .row.justify-content-center.p-4
          .col-lg-10.col-12.order-lg-2.order-2
            p Los precintos solo pueden ser #[b retirados o rotos] por personal autorizado (ej. funcionarios del ICA) o en el punto de destino (planta de beneficio o granja receptora), bajo la supervisin de un responsable. La #[b rotura en ruta] solo se permite en caso de #[b emergencia] (Ej. Plan de contingencia por animales cados o atrapados) y debe ser #[b notificada y registrada] inmediatamente.
            figure
              img(src='@/assets/curso/temas/tema1/img-22.png' alt="Imagen decorativa")
      .bg-color-2.py-3.py-md-4(titulo="Seguridad del precinto")
        .row.justify-content-center.p-4
          .col-lg-10.col-12.order-lg-2.order-2
            p Los precintos suelen ser de materiales resistentes (plstico de alta seguridad o metal) y llevar un #[b nmero de serial nico], que es el elemento de trazabilidad.
            figure
              img(src='@/assets/curso/temas/tema1/img-23.png' alt="Imagen decorativa")
    
    Separador

    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.7 Tcnicas de contencin y tipos de reas

    p.mb-4 La #[b contencin] en el transporte terrestre se define como la tcnica y la infraestructura destinadas a #[b asegurar fsicamente] a los animales dentro de la unidad de transporte, previniendo su escape, minimizando el movimiento excesivo que causa lesiones (golpes, cadas) y protegiendo el bienestar animal.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tcnicas de contencin

    p.mb-4 Las tcnicas de contencin se aplican a nivel de la estructura del vehculo, a nivel interno (organizacin de la carga) y a nivel de unidades de transporte (cajas). A continuacin, ms detalle:

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 A. Tcnicas de contencin estructural (la unidad de transporte)

    .BGM01.px-md-5.px-4.mb-4
      p.mb-4 El mtodo ms bsico de contencin es asegurar que la carrocera sea inherentemente segura y contenga la carga viva; adems de ello, deben contener lo siguiente:

      .row.justify-content-center.mb-0
        .col-lg-10
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 17.
            i Tcnicas de contencin
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de contencin
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Requisito tcnico
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Diseo integral]
                  td(style= "background-color: #FFFF") La unidad de transporte debe estar diseada especficamente para la #[b contencin de animales vivos].
                  td(style= "background-color: #FFFF") La unidad debe estar diseada para la #[b contencin de animales vivos]. Resolucin 20223040006915 (Nm. 2.2.c.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Seguridad de paredes]
                  td(style= "background-color: #FFFF") Las paredes internas deben ser #[b lisas, seguras, sin salientes, ni bordes puntiagudos] para contener al animal sin causar lesiones.
                  td(style= "background-color: #FFFF") "Sern lisas, #[b seguras], sin salientes, ni bordes puntiagudos que puedan causar lesiones...". Resolucin 20223040006915 (Nm. 2.2.d.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Fijacin del suelo]
                  td(style= "background-color: #FFFF") El piso debe ser #[b antideslizante] para que el animal pueda mantener la #[b posicin de estacin de equilibrio esttico] (autocontencin).
                  td(style= "background-color: #FFFF") "El piso deber ser #[b antideslizante..."]. Resolucin 20223040006915 (Nm. 2.2.c.ii).

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 B. Tcnicas de contencin interna (separacin)

    p.mb-4 El mtodo de contencin interna se realiza mediante la #[b separacin fsica] para controlar la interaccin social de la carga viva, evitando que el conflicto anule la contencin individual. De esta tcnica, se destaca lo siguiente:

    .row.mb-4
      .col-lg-4(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img-24.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Implemento
              p.text-center.mb-0 Divisiones o barreras fuertes.
      .col-lg-4(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img-25.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Propsito
              p.text-center.mb-0 La contencin interna se aplica para separar grupos hostiles (ej. con cuernos), animales de diferente tamao y machos sexualmente maduros de hembras.
      .col-lg-4(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img-26.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Soporte Documental
              p.text-center.mb-0 "Contar con #[b divisiones o barreras fuertes] que permitan la #[b separacin de los animales"]. Manual de procedimiento (Nm. 2.2.c.iii).

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 C. Tcnicas de contencin de unidades (especies menores)

    .row.justify-content-center.mb-4
      .col-lg-7.order-lg-1.order-2.mb-lg-0
        p(data-aos="fade-left") Para las especies transportadas en cajas o guacales (ej. pollitos de un da), la tcnica se centra en asegurar la unidad dentro del vehculo, basado en lo siguiente: 
        .tarjeta.bg-color-4.p-4.mb-4
          .row.justify-content-center.align-items-center
            .col-lg-12
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p #[b Aseguramiento]
                    br
                    | Las cajas deben estar #[b fijadas o dispuestas] de tal manera que se evite su #[b desplazamiento o volcamiento] durante el trayecto, asegurando la contencin de la unidad primaria.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Soporte documental]
                    br
                    | Las cajas deben estar #[b "fijadas o dispuestas de manera tal, que se evite su desplazamiento y/o volcamiento"]. Resolucin 20223040006915 (Nm. 2.2.c.v).
      .col-lg-5.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img-27.png" alt="Imagen decorativo")

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tipos de reas relevantes en el proceso de contencin

    p.mb-4 El proceso de transporte involucra diferentes reas que tienen requisitos especficos para la contencin y el bienestar:

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 18.
          i reas del proceso de transporte
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tipo de rea
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Concepto y definicin en el contexto del manual
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Requisito de contencin / bienestar
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF")  #[b rea de contencin/transporte (carrocera)]
                td(style= "background-color: #FFFF")  El espacio fsico dentro del vehculo (compartimentos o corrales) donde la carga permanece durante el trayecto.
                td(style= "background-color: #FFFF")  Debe procurar #[b espacio disponible] para que el animal pueda #[b levantarse, acostarse, dar vuelta] (contencin funcional).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF")  #[b rea de manejo/transicin (rampas/plataformas)]
                td(style= "background-color: #FFFF")  El espacio de infraestructura utilizado para el #[b cargue y descargue] (embarcaderos). Es una zona de contencin temporal y movimiento.
                td(style= "background-color: #FFFF")  Debe tener #[b pisos antideslizantes] y un #[b mximo 20 grados de inclinacin] (contencin segura para la movilidad).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF")  #[b rea de descanso/atencin (paradas)]
                td(style= "background-color: #FFFF")  El lugar de descanso programado o necesario en ruta. Es un espacio de contencin externa al vehculo, pero seguro, donde se interrumpe la movilizacin.
                td(style= "background-color: #FFFF")  Debe permitir la #[b hidratacin o alimentacin] y la #[b inspeccin] (contencin con atencin de bienestar).

    .row.justify-content-center.align-items-center
      .col-lg-10
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img-11.svg' alt="Imagen decorativa")
          .tarjeta.color-primario2.w-100(style="padding-top: 5px !important")
            .text-white.p-4
              p.text-center.mb-0(style="margin-top: 42px;") #[b Soporte documental (rea de descanso):] la interrupcin se hace en un #[b "lugar de descanso de los animales] para realizar actividades de inspeccin, acomodacin, #[b hidratacin o alimentacin"]. Manual de procedimiento (Nm. 4.h).

    Separador

    #t_1_8.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.8 Tcnicas de conduccin de animales

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-4(style="background-color:#004E3D")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/img-29.png')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
        p.mb-0 Las #[b tcnicas de conduccin] en el transporte terrestre de animales en pie, se refieren tanto al #[b manejo del vehculo] (conduccin suave y programada) como a la #[b aplicacin de principios etolgicos] (comportamiento animal) durante el cargue y descargue. El objetivo de estas tcnicas es #[b minimizar el estrs, el miedo, las cadas y las lesiones] de los animales.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Tcnicas de conduccin vehicular (manejo suave y controlado)

    p.mb-4 La conduccin es un factor de bienestar directo, ya que los movimientos bruscos afectan el equilibrio esttico de los animales. Por consiguiente, se debe tener en cuenta lo siguiente:

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 19.
          i Tcnicas de conduccin vehicular
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de conduccin
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle operacional
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte normativo (base del bienestar)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Aceleracin y frenado progresivo]
                td(style= "background-color: #FFFF") El conductor debe evitar los #[b frenazos bruscos] y las aceleraciones repentinas. La tcnica debe ser #[b suave y estable] para evitar que la inercia desequilibre a los animales.
                td(style= "background-color: #FFFF") Evitar el #[b empleo de fuerza innecesaria] y el #[b hostigamiento] que causa la conduccin brusca. Manual de procedimiento (Nm. 4).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Control en curvas]
                td(style= "background-color: #FFFF") Las curvas y los giros deben tomarse a #[b velocidad reducida y de forma gradual] para mitigar la #[b fuerza centrfuga] que empuja a los animales contra las paredes o las divisiones, causando golpes.
                td(style= "background-color: #FFFF") Implcito en la necesidad de evitar lesiones y traumatismos.
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Conduccin programada (descanso)]
                td(style= "background-color: #FFFF") La tcnica de conduccin incluye planificar el #[b descanso obligatorio] del personal para evitar la fatiga, lo cual compromete la capacidad de reaccionar suavemente.
                td(style= "background-color: #FFFF") #[b Descansar al menos ocho (8) horas] tras #[b ocho (8) horas continuas] de conduccin. Manual de procedimiento (Nm. 4.i).
              tr(data-aos="fade-right")
